.main-footer__social {
  display: block;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 280px;
  background-color: #F2F2F2;
  padding: 28px 0 22px;
}

.main-footer {
  margin: 0;
  flex-shrink: 0;
  background-color: #F2F2F2;
}

.main-footer__vector:hover {
  opacity: 0.8;
}

.main-footer__vector:active {
  opacity: 0.6;
}

@media (min-width: $tablet-width) {
  .main-footer__social {
    display: flex;
    margin: 0;
    width: 712px;
    padding: 38px 0 41px;
  }

  .main-footer__social--index {
    width: 718px;
    justify-content: space-between;
  }

  .main-footer__social--catalog,
  .main-footer__social--form {
    justify-content: space-between;
    padding: 38px 0 40px;
  }

  .main-footer__vector--catalog {
    width: 144px;
    height: 46px;
  }

  .main-footer__vector {
    margin-top: 5px;
  }

}


@media (min-width: $desktop-width) {
  .main-footer__social {
    width: 1216px;
    justify-content: space-between;
  }

  .main-footer__social--catalog {
    width: 1217px;
    justify-content: space-between;
    margin-top: 8px;
    padding: 38px 0 50px;
  }

  .main-footer__social--form {
    width: 1217px;
    justify-content: space-between;
    margin-top: 8px;
    padding: 38px 0 52px;
  }

  .main-footer__vector {
    margin-top: 9px;
    margin-left: -10px;
  }
}
