.page {
  height: 100%;
  font-size: 14px;
  font-family: "Oswald", "Arial", sans-serif, "Arial", sans-serif;
  line-height: 18px;
}

.page__body {
  background-color: #ffffff;
  margin: 0;
  min-height: 100%;
  color: #000000;
  display: flex;
  flex-direction: column;
}

.page-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  margin: 0 auto;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  justify-content: center;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-decoration: none;
  margin: 0;
  cursor: pointer;
}

.button--grey {
  background-color: #F2F2F2;
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  padding: 10px 90px;
  margin: 8px 0 43px;
  width: 100px;
}

.button--grey:hover {
  background-color: #EBEBEB;
}

.button--grey:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--green {
  background: #68b738;
}

.button--green:hover {
  background-color: #5EAA2F;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
}

